@import url(https://fonts.googleapis.com/css?family=Expletus+Sans);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
    margin: 0;
    padding: 0;
}

h1,h2{margin:10px auto 5px;font-family:'Ubuntu', sans-serif;font-size:80px;font-weight:bold;text-transform:uppercase;color:#121212;text-align:center;letter-spacing:5px;text-shadow:2px 7px 5px rgba(0,0,0,0.3),0 -4px 10px rgba(255,255,255,0.3)}h2{font-size:45px;margin:5px auto 10px}h2.main-star{background-image:-webkit-gradient(linear, left top, right top, color-stop(0, #f22), color-stop(0.15, #f2f), color-stop(0.3, #22f), color-stop(0.45, #2ff), color-stop(0.6, #2f2), color-stop(0.75, #2f2), color-stop(0.9, #ff2), color-stop(1, #f22));color:transparent;-webkit-background-clip:text}

.main-area{width:650px;margin:0 auto;padding:10px 5px;background:aliceblue;border-radius:5px;margin-bottom:50px}.main-area.game-mode{width:800px}.main-menu{text-align:center;margin:20px 0}.main-menu .property .title{margin-bottom:15px}.main-menu .property .tank-title,.main-menu .property .instruction-title{margin-top:15px}.main-menu .property .map-section{width:600px;margin:0 auto}.main-menu .property .map-section img{border:2px solid black;width:500px;min-height:315px}.main-menu .property .tank-section{width:600px;margin:0 auto}.main-menu .property .tank-image{border:2px solid black;width:500px;height:315px;margin:0 auto 5px;display:flex;justify-content:center;align-items:center;background-image:url(/static/media/tank-bg.27f72480.jpg);background-repeat:no-repeat;background-size:cover}.main-menu .property .tank-image img{width:200px}.main-menu .property .instruction-content{border:2px solid black;width:450px;height:285px;margin:0 auto;background-image:url(/static/media/instruction.ef5095cf.png);background-repeat:no-repeat;background-size:150px;background-position-x:344px;background-position-y:8px;text-align:left;text-align:initial;padding:15px 25px}.main-menu .property .instruction-content p{margin:5px}.main-menu .property .instruction-content p span{color:#004d9e;font-size:25px;display:inline-block;margin-left:10px}.main-menu .property .instruction-content p span.left{transform:rotate(180deg)}.main-menu .property .instruction-content p span.up{transform:rotate(270deg)}.main-menu .property .instruction-content p span.down{transform:rotate(90deg)}.main-menu .property .instruction-content p span.space{transform:rotate(90deg);padding:0 0 8px 0;font-size:28px}.main-menu .property .instruction-content h4{margin:10px 0}.main-menu .btn-start{margin:20px 15px 15px}.main-menu .btn-start button{width:50%}

.game-area{min-height:750px;min-width:800px}.game-area .end-game{text-align:right;margin:10px 25px}.game-area .battlefield{width:750px;height:700px;border:2px solid black;margin:15px auto}.win-lose-image img{width:100%}.win-lose-result{text-align:center;font-size:22px}.win-lose-result h2{margin:60px 0 20px 0}.win-lose-result>div{margin-bottom:5px}.game-info-block{width:750px;margin:5px auto}.game-info-block .game-info{display:flex}.game-info-block .game-info>div{width:50%}.game-info-block .game-info-title{font-family:cursive;font-weight:600;margin-bottom:2px}.game-info-block .game-info-title.enemy{text-align:right}.game-info-block .enemy-count{text-align:right;font-family:cursive;font-weight:600;margin-right:5px}progress[value]{-webkit-appearance:none;-moz-appearance:none;appearance:none;border:none;width:100%;height:20px;background-color:whiteSmoke;border-radius:3px;box-shadow:0 2px 3px rgba(0,0,0,0.5) inset;color:royalblue;position:relative}progress[value]::-webkit-progress-bar{background-color:whiteSmoke;border-radius:3px;box-shadow:0 2px 3px rgba(0,0,0,0.5) inset}progress[value]::-webkit-progress-value{position:relative;background-size:35px 20px, 100% 100%, 100% 100%;border-radius:3px;animation:animate-stripes 5s linear infinite}@keyframes animate-stripes{100%{background-position:-100px 0}}.life::-webkit-progress-value{background-image:-webkit-linear-gradient(135deg, transparent, transparent 33%, rgba(0,0,0,0.1) 33%, rgba(0,0,0,0.1) 66%, transparent 66%),-webkit-linear-gradient(top, rgba(255,255,255,0.25), rgba(0,0,0,0.2)),-webkit-linear-gradient(left, red, orange, yellow, green)}

footer{background:#1A1A1A;position:relative}footer .footer_bottom{padding:20px 0;color:#6d6e71;background:#0D0D0D;overflow:hidden}footer .footer_bottom .container-fluid{padding-left:20px;padding-right:20px}footer .footer_bottom .copyright_column{text-align:center}footer .footer_bottom .copyright_column #menu-copyright{display:inline-block}footer .footer_bottom .copyright_column #menu-copyright li{display:inline-block;margin:0 5px 5px 0;text-align:center;opacity:60%;color:#fff;list-style-type:none}footer .footer_bottom #menu-copyright li a{color:#fff;font-size:15px;font-weight:700;line-height:30px;opacity:60%;text-decoration:none;font-family:proxima-nova,Helvetica,sans-serif;outline:none !important;transition:all .5s ease}footer .footer_bottom #menu-copyright li a:hover{opacity:100%;box-shadow:0 4px 2px -2px #fff;transition:all .3s ease}footer .footer_bottom .copyright_column #menu-copyright li::after{content:"•";display:inline-block;margin-left:6px;box-sizing:border-box}

html,body{height:100%;margin:0}body{background-color:#0057ff}#root .App{display:flex;flex-flow:column;min-height:100vh}#root .header{flex:0 1 auto}#root main.container{flex:1 1 auto}#root .footer{flex:0 1 auto}

